<template>
    <div>
        <el-dialog
  title=""
  :visible.sync="dialogVisible"
  width="70%"
  :before-close="handleClose">
  <el-button @click="up"></el-button>
  <el-button @click="down"></el-button>
<!-- page rotate  -->
{{currentPage}}/{{pageCount}}
<el-button @click="print"></el-button>

  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false"> </el-button>
    <el-button type="primary" @click="dialogVisible = false"> </el-button>
  </span>
</el-dialog>
    </div>
  </template>
  
  <script>
  
  import pdf from 'vue-pdf'
  export default {
    components:{
        pdf
    },
    data() {
      return {
        currentPage:1,
        pageCount:2,
        dialogVisible: false,
        page:1
      };
    },
    methods: {
        print(){
            this.$refs.mypdf.print();
        },
        up(){
            if(this.page<this.pageCount){
                this.page=this.page+1
            }
        },
        down(){
            if(this.page>1){
                this.page=this.page-1
            }
        },
      handleClose(done) {
        this.$confirm('')
          .then(_ => {
            done();
          })
          .catch(_ => {});
        }
    }
    
  }
  </script>
  
  <style>
  
  </style>